<template>
  <div>
    <b-modal
      variant="dark"
      header-bg-variant="dark"
      header-text-variant="light"
      content-class="border-dark"
      v-model="showModal"
    >
      <!-- title -->
      <template v-slot:modal-title>
        {{ modalTitle }}
      </template>
      <!-- body -->
      <template v-slot:default>
        <div class="modal_body">
          <p v-html="modalConfirm"></p>
          <p class="red">
            * {{ modalWarnning }}
          </p>
        </div>
      </template>
      <!-- footer -->
      <template v-slot:modal-footer>
        <b-button
          variant="primary"
          @click="modalSubmit"
        >
        {{ $t('common.ok') }}
        </b-button>
        <b-button
          variant="dark"
          @click="hide()"
        >
          {{ $t('common.cancel') }}
        </b-button>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: 'twoButtonAlert',
  props: {
  },
  data () {
    return {
      showModal: false,
      data: {
        modalType: '',
        itemType: '',
        getItems: []
      }
    }
  },
  computed: {
    getName: function () {
      let _name = 'none'
      switch (this.data.itemType) {
        case 'project':
          _name = this.data.getItems
          break
        case 'version':
          _name = this.data.getItems[0].versionName
          break
        case 'utterance':
          _name = this.data.getItems[0].productInfo
          break
        default:
          break
      }
      return _name
    },
    modalTypeKo () {
      return this.$i18n.t('common.' + this.data.modalType)
    },
    itemTypeKo () {
      return this.$i18n.t('popup.' + this.data.itemType)
    },
    remainCount () {
      return this.data.getItems.length
    },
    modalActive () {
      let _active = 'none'
      switch (this.data.modalType) {
        case 'delete':
          _active = this.$i18n.t('common.recoveryIs')
          break
        case 'complete':
          _active = this.$i18n.t('common.modify')
          break
        case 'close':
          _active = this.$i18n.t('common.modify')
          break
        default:
          break
      }
      return _active
    },
    modalConfirm () {
      return this.$i18n.t('popup.modalConfirm', {
        name: this.getName,
        remainCount: this.remainCount,
        modalType: this.modalTypeKo
      })
    },
    modalTitle () {
      return this.$i18n.t('popup.modalTitle', { itemType: this.itemTypeKo, modalType: this.modalTypeKo })
    },
    modalWarnning () {
      return this.$i18n.t('popup.modalWarnning', { modalType: this.modalTypeKo, active: this.modalActive })
    }
  },
  methods: {
    modalSubmit () {
      this.$emit('submit', this.data)
    },
    show (_data) {
      this.data = {
        modalType: _data.modalType,
        itemType: _data.itemType,
        getItems: _data.getItems
      }
      this.showModal = true
    },
    hide () {
      this.showModal = false
    }
  }
}
</script>

<style scoped>
.modal_body{
  min-height: 100px;
  padding-top: 20px;
}
.modal_body > p{
  font-size: 15px;
  font-weight: 700;
}
.modal_body > p > span{
  font-size: 13px;
  font-weight: 500;
  color: #777;
}
.modal_body > p.red{
  color: red;
  font-size: 13px;
  margin-top: 50px;
}
</style>
