<template>
  <b-container fluid>
    <b-row class="d-flex justify-content-start">
      <b-col cols="4">
        <b-form-group>
          <b-input-group size="sm">
            <b-form-input
              type="search"
              v-model="search"
              id="filterInput"
              :placeholder="$t('dashboard.dashboardPlaceholder')"
            ></b-form-input>
            <b-input-group-append>
              <b-button variant="primary" :disabled="!search" @click="search = ''">
                {{ $t('common.clear') }}
              </b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-row>
    <b-list-group>
      <b-list-group-item
        v-model="updated"
        href="#"
        class="flex-column pt-4 pb-4"
        v-for="(item, loopIdx) in filteredList"
        :key="loopIdx"
      >
        <div
          class="d-flex justify-content-start align-items-center"
          @click.stop="selProject(item.metadata.name)"
        >
          <div class="title_area p-2 pr-5">
            <h3 class="mb-1 text-primary">{{ item.metadata.name }}</h3>
            <small class="m-0">{{getCreationInfo(item)}}</small>
          </div>
          <div
            class="d-flex"
            v-for="item in getStorageByName(item.metadata.name)"
            :key="item.metadata.name"
          >
            <div class="storage d-flex align-items-center pr-2">
              <div class="storage_icon p-3">
                <h3><i class="fa fa-database"></i></h3>
              </div>
              <div class="storage_info mb-2">
                <small>
                  <b>{{item.metadata.name}}</b><br />
                  {{item.spec.resources.requests.storage}}
                  <span v-if="item.status.phase" class="ml-1 mr-1"> | </span>
                  {{item.status.phase}}
                </small>
              </div>
            </div>
          </div>

          <div class="ml-auto">
            <b-button
              variant="link"
              class="btn pull-right"
              @click.stop="delProject(item.metadata.name)"
            >
              <i class="fa fa-trash"></i> {{ $t('common.delete') }}
            </b-button>
          </div>
        </div>
      </b-list-group-item>
    </b-list-group>
    <one-button-alert
      name="OneButtonAlert"
      ref="OneButtonAlert"
    />
    <two-button-alert
      name="TwoButtonAlert"
      ref="TwoButtonAlert"
      @submit="proDeleteSubmit"
    />
  </b-container>
</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex'
import OneButtonAlert from '../components/modal/OneButtonAlert.vue'
import twoButtonAlert from '../components/modal/TwoButtonAlert.vue'
import { timeSince } from '../utils/Date'

export default {
  name: 'Dashboard',
  components: {
    OneButtonAlert,
    twoButtonAlert
  },
  props: {
  },
  data () {
    return {
      updated: false,
      search: ''
    }
  },
  computed: {
    ...mapState('project', ['projectList']),
    ...mapGetters('project', ['getProjectCount']),
    ...mapState('storage', ['storageList']),
    projectCountUpdated () {
      return this.getProjectCount
    },
    filteredList () {
      return this.projectList.items.filter(project => {
        return project.metadata.name.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  },
  mounted () {
    this.requestStorage()
  },
  methods: {
    ...mapActions('project', ['setCurrentProject', 'deleteProject']),
    ...mapActions('storage', ['getStorageList']),
    selProject (projectName) {
      this.setCurrentProject(projectName)
      this.$router.push({
        name: 'main',
        params: { projectName: projectName }
      })
    },
    delProject (projectName) {
      this.$refs.TwoButtonAlert.show({
        modalType: 'delete',
        itemType: 'project',
        getItems: projectName
      })
    },
    proDeleteSubmit (data) {
      this.deleteProject(data.getItems)
        // eslint-disable-next-line handle-callback-err
        .catch(error => {
          this.$refs.OneButtonAlert.show({
            title: this.$i18n.t('popup.delProjectTitle'),
            text: error,
            confirm: false
          })
        })
      // hide popup
      this.$refs.TwoButtonAlert.hide()
    },
    getStorageByName (name) {
      let storage = this.storageList.get(name)
      return storage !== undefined ? storage.items : []
    },
    getCreationInfo (item) {
      return 'created by ' + item.metadata.annotations['openshift.io/requester'] +
        ' ' + timeSince(item.metadata.creationTimestamp)
    },
    requestStorage () {
      this.projectList.items.forEach((p, index, arr) => {
        this.getStorageList(p.metadata.name).then((list) => {
          this.updated = !this.updated
        })
      })
    }
  },
  watch: {
    projectCountUpdated () {
      this.requestStorage()
    }
  }
}
</script>

<style scoped>
.title_area{
  border-right: 1px solid #ddd;
}
.storage{
  color: #999;
}
.storage .storage_info{
  line-height: 15px;
}
.storage .storage_info span{
  color: #bbb;
}
</style>
